import React from 'react'
import img from './kronos_logo.png'

function MainKHeader(){
    return(
        <div className="maink-header">
            <div className="maink-header2">
                <div className="maink-container">
                    <h1>MENU</h1>
                </div>
                <div className="maink-logo">
                    <a href="https://krdannylo.github.io/gh-pages-krovid/">
                        <img className="maink-kronos_logo" src={img} alt="kronos logo"/>
                    </a>
                </div>
                <div className="icon-menu">
                    <svg width="10em" height="1.9em" viewBox="0 0 16 16" class="bi bi-list" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" d="M2.5 11.5A.5.5 0 0 1 3 11h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 7h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 3h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
                    </svg>
                </div>
            </div>
        </div>
    )
}

export default MainKHeader