import React from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';
import MainK from './Pages/MainK'
import BuyK from './Pages/BuyK'

function Routes(){
    return(
        <BrowserRouter>
            <Switch>
                <Route path="/gh-pages-krovid" exact component={MainK} />
                <Route path="/buy" exact component={BuyK} />
            </Switch>
        </BrowserRouter>
    )
}

export default Routes