import React from 'react'
import img from './kronos_logo.png'
import img1 from './youtube.png'
import img2 from './instagram.png'

function MainKFooter(){
    return(
        <div className="maink-footer">
            <div className="maink-back">
                <footer id="maink-footer"> 
                    <div className="maink-footer-text">
                        <p>Informações de contato: kronosfll1a@gmail.com</p>
                    </div>
                    <div className="maink-footer-text2">
                        <p>Esse site é meramente ilustrativo pois o produto não está a venda e foi criado pela Equipe</p>
                    </div>
                    <div className="maink-footer-text3">
                        <p>de robótica Kronos em sua participação do Desafio COVID-19 em setembro de 2020.</p>
                    </div>
                    <div className="maink-footer-logo">
                        <a href="https://krdannylo.github.io/gh-pages-krovid/">
                             <img className="maink-kronos_logo_footer" src={img} alt="kronos logo3"/>
                        </a>
                    </div>
                    <div className="maink-footer-icons">
                        <a href="https://www.instagram.com/_kronosrs/">
                            <img className="maink-footer-icon-insta" src={img2} alt="insta"/>
                        </a>
                        <a href="https://www.youtube.com/channel/UCTJG5nir9HSvRrRa3PfQ4-Q">
                            <img className="maink-footer-icon-yout" src={img1} alt="play"/>
                        </a>
                    </div>
                </footer>
            </div>
        </div>

    )
}

export default MainKFooter